.burger {
  cursor: pointer;
  position: fixed;
  top: 0;
  right: 0;
  width: 100px;
  height: 100px;
  z-index: 6000;
}

@media (orientation: landscape) {
  .burger {
    display: none;
  }
}

@media (min-width: 700px) {
  .burger {
    display: none;
  }
}

.burger__path {
  fill: none;
  stroke: #ffffff;
  stroke-width: 3;
  stroke-linecap: round;
  stroke-linejoin: round;
  --length: 24;
  --offset: -38;
  stroke-dasharray: var(--length) var(--total-length);
  stroke-dashoffset: var(--offset);
  transition: all 0.8s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.burger__path--1, .burger__path--3 {
  --total-length: 111.22813415527344;
  --offset: -50.22813415527344;
}

.burger__path--2 {
  --total-length: 99;
}

.burger--selected .burger__path--1, .burger--selected .burger__path--3 {
  --length: 22.627416998;
  stroke: #000;
}

.burger--selected .burger__path--2 {
  --length: 0;
}

.burger--selected .burger__path {
  transform: translateX(30px);
}

.burger--selected .burger__path--1, .burger--selected .burger__path--3 {
  --offset: -16.9705627485;
}

.burger--selected .burger__path--2 {
  --offset: -20;
}

